.square {
    max-width: 100%;
    border: 1px solid rgb(31, 30, 30);
    border-radius: 5px;
    cursor: pointer;
    -webkit-box-shadow: 2px 2px grey;
            box-shadow: 2px 2px grey;
}

.square:hover {
    border: 1px solid rgb(133, 5, 5);
    -webkit-box-shadow: 2px 2px rgb(223, 216, 18);
            box-shadow: 2px 2px rgb(223, 216, 18);
    opacity: 0.7;
}


